import React from 'react';
import { Row, Col, Card, Form, Button, InputGroup, FormControl, DropdownButton, Dropdown } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";


import ErrorBody from '../../App/components/Reusables/ErrorBody';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import { validateTaxCreation } from '../../validation/tax';
import { createTax } from '../../api/misc';
import { getRole, updateRole } from '../../api/userManagement/roles';
import Loader from '../../App/components/Reusables/Loader';
import MarkBox from '../../App/components/Reusables/MarkBox';
import { validateUserCreation } from '../../validation/user';
import { validateRoleCreation } from '../../validation/role';



const Create = (props) => {
    const [loading, setLoading] = React.useState(true);
    const [submitting, setSubmitting] = React.useState(false)
    const [powersArr, setPowersArr] = React.useState([]);
    const [powerValuesArr, setPowerValuesArr] = React.useState([])
    const [roleData, setRoleData] = React.useState({});

    const [errors, setErrors] = React.useState({});

    React.useEffect(() => {
        const controller = new AbortController();
        getRole({ action: 'get', role_id: props.match.params.id }, controller.signal)
            .then(res => {
                (async () => {
                    console.log(res.json)
                    if (res.json.status) {
                        const role = res.json.roles[0];
                        await setPowersArr(Object.keys(role.powers))
                        await setPowerValuesArr(Object.values(role.powers))
                        delete role.powers;
                        await setRoleData(role)
                        setLoading(false);

                    }
                })()
            })
            .catch(err => {
                console.log(err)
            })
        return () => controller.abort()
    }, [])

    const _roleDataChange = (e) => {
        setErrors({ ...errors, [e.target.name]: '' })
        setRoleData({
            ...roleData,
            [e.target.name]: e.target.value
        })
    }

    const _markAll = () => {
        const tempPowerValues = [...powerValuesArr];
        for (let i in powersArr) {
            tempPowerValues[i] = true;
        }
        setPowerValuesArr(tempPowerValues)
    }

    const _unmarkAll = () => {
        const tempPowerValues = [...powerValuesArr];
        for (let i in powersArr) {
            tempPowerValues[i] = false;
        }
        setPowerValuesArr(tempPowerValues)
    }


    const _toggle = (index) => {
        const tempPowerValues = [...powerValuesArr]
        tempPowerValues[index] = !tempPowerValues[index];
        setPowerValuesArr(tempPowerValues)
    }

    const _submit = () => {

        const { isValid, errors } = validateRoleCreation(roleData);
        if (!isValid) {
            Jhilikka.fire({ icon: 'error', title: 'Fix errors and retry submission!', background: '#ffe5de' })
            setErrors({ ...errors })
            return;
        };
        setSubmitting(true)
        //if no errros api call
        const requestJson = {
            "action": "update",
            "role_id": roleData.id,
            "name": roleData.name,
            "description": roleData.description || '',
            "powers": powersArr,
            "values": powerValuesArr
        }
        console.log(JSON.stringify(requestJson));
        updateRole(requestJson)
            .then((res) => {
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Role updated successfully!', background: '#dffff0' })
                    props.history.push('/roles');
                } else {
                    setSubmitting(false)
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                setSubmitting(false)
                console.log(err)
            });
    }




    const PowersList = powersArr.map((instance, index) => {
        let instanceName = instance.replace(/_/g, ' ');
        return <Col md={4} key={index}>
            <div
                style={{
                    cursor: 'default',
                    display: 'flex',
                    justifyContent: 'flex-start',
                    marginBottom: '8px',
                    marginLeft: '2px',
                }}
                onClick={() => _toggle(index)}
            >
                <div className='mr-1'><MarkBox status={powerValuesArr[index]} /></div>
                <div style={{ lineHeight: '21.25px' }}>{instanceName[0].toUpperCase() + instanceName.slice(1)}</div>
            </div>

        </Col>
    })


    if (loading) return <Loader />
    return (
        <Aux>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Update Role</h5>
                            <hr />
                            <Row>

                                <Col md={12}>
                                    <Form.Group  >
                                        <Form.Label>Role Name*</Form.Label>
                                        <Form.Control name="name" value={roleData.name} onChange={_roleDataChange} type="text" placeholder="Role Name" />
                                        <ErrorBody>{errors.name}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={12}>
                                    <Form.Group  >
                                        <Form.Label>Description</Form.Label>
                                        <Form.Control name="description" as="textarea" rows="6" value={roleData.description} onChange={_roleDataChange} type="text" placeholder="Description" />
                                        <ErrorBody>{errors.description}</ErrorBody>
                                    </Form.Group>
                                </Col>
                            </Row>
                            <br />
                            <h5 style={{ fontSize: "1.2em" }}>Permissions</h5>
                            <span className='mr-2' style={{ cursor: 'pointer', color: '#007bff', fontSize: '0.9em' }} onClick={_markAll}>Mark All</span>
                            <span className='mr-2' style={{ cursor: 'pointer', color: '#007bff', fontSize: '0.9em' }} onClick={_unmarkAll}>Unmark All</span>
                            <hr />
                            <Row>
                                {PowersList}
                            </Row>
                            <div className="d-flex justify-content-end" >
                                <Button
                                    variant="warning"
                                    onClick={() => { props.history.push('/roles') }}
                                >
                                    Cancel
                                    </Button>
                                <Button
                                    disabled={submitting}
                                    variant="primary"
                                    onClick={_submit}
                                >
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Submitting</> : 'Submit'}
                                </Button>
                            </div>

                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Aux>
    );
}

export default Create;
