

import React from 'react';

const MarkBox = (props) => {
    const status = props.status;
    return <button
        className={status ? `btn btn-primary btn-sm` : `btn btn-outline-primary btn-sm`}
        style={{ padding: '0px 3px', margin: 0, marginLeft: 0, height: '21.18px', width: '21.11px' }}
    ><i className={`feather icon-${status ? 'check' : null} text-c-white f-8 m-0`} /></button>
}

export default MarkBox;