import isEmpty from "./isEmpty"


const validateTaxCreation = (taxData) => {


    const errors = {}


    if (isEmpty(taxData.name)) {
        errors.name = 'Name of tax cannot be empty!'
    }
    if (isEmpty(taxData.type)) {
        errors.type = 'Provide type of tax!'
    }
    if (isEmpty(taxData.rate)) {
        errors.rate = 'Provide rate of tax!'
    }


    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}

export { validateTaxCreation }